import React from "react"
import { Helmet } from "react-helmet"
import '../styles/style.scss'
import Layout from '../components/Layout'

export default function Error() {
  return (
    <Layout>
      <Helmet>
        <title>Error</title>
      </Helmet>
      <div className="error-page">Esta página no existe.</div>
    </Layout>
  )
}
